.footer__wrapper{max-width:140rem;width:100%;margin:0 auto}.footer{background-color:#fff;padding:1.5rem 2.5rem}.footer__wrapper{text-align:center}.footer__copyright{color:#999}

.error-boundary{min-height:100vh;background-image:linear-gradient(to right, #4A00E0, #8E2DE2);display:flex;flex-direction:column;justify-content:space-between}.error-boundary__content{flex-grow:1;display:flex;justify-content:center;align-items:center}.error-boundary__body{padding:2.5rem;border-radius:1.5rem;background-color:#fff;max-width:45rem;box-shadow:0 0.8rem 1.5rem rgba(39,39,39,0.2);text-align:center}.error-boundary__top{display:flex;justify-content:center;align-items:center;margin-bottom:1.5rem}.error-boundary__title{font-size:2.3rem}.error-boundary__icon{font-size:2rem;margin-left:1rem;position:relative;top:.1rem}.error-boundary__text{text-align:left;margin-bottom:2.5rem}.error-boundary__link{color:#fd9d01}

.input-wrapper{position:relative;max-width:-webkit-max-content;max-width:-moz-max-content;max-width:max-content}.input-wrapper__label{color:#999;cursor:text;position:absolute;top:calc(50% - 1.6rem / 2);left:1rem;transition:top 400ms ease, left 400ms ease, font-size 400ms ease}.input-wrapper__label--top{top:-1.2rem;left:0;font-size:1.2rem}.input-wrapper__input{display:block;min-width:30rem;outline:none;background-color:rgba(0,0,0,0);border:none;border-bottom:0.2rem solid #999;padding:1rem;font-family:"Raleway",sans-serif;font-size:1.6rem;transition:border-color 200ms ease}.input-wrapper__input:focus{border-color:#fd9d01}

.button{display:block;border:none;font-size:1.6rem;font-family:"Raleway",sans-serif;padding:1.1rem 3rem;border-radius:2rem;cursor:pointer;outline:none;transition:background-color 200ms ease, color 200ms ease, box-shadow 200ms ease}.button--orange{background-color:#fd9d01;box-shadow:0 0.5rem 1.2rem rgba(253,157,1,0.7);color:#fff}.button--orange:hover{background-color:#e48d01}.button--orange:focus{box-shadow:0 0.5rem 1.2rem rgba(253,157,1,0.7),inset 0 0 0 0.5rem #fd9d01,inset 0 0 0 0.65rem #fff}.button--blue{background-color:#29b6cf;box-shadow:0 0.5rem 1.2rem rgba(41,182,207,0.4);color:#fff}.button--blue:hover{background-color:#25a3ba}.button--blue:focus{box-shadow:0 0.5rem 1.2rem rgba(41,182,207,0.4),inset 0 0 0 0.5rem #29b6cf,inset 0 0 0 0.65rem #fff}.button--red{background-color:#e71616;box-shadow:0 0.5rem 1.2rem rgba(231,22,22,0.4);color:#fff}.button--red:hover{background-color:#d01414}.button--red:focus{box-shadow:0 0.5rem 1.2rem rgba(231,22,22,0.4),inset 0 0 0 0.5rem #e71616,inset 0 0 0 0.65rem #fff}.button:disabled{cursor:not-allowed;background-color:rgba(153,153,153,0.15);color:#999;box-shadow:0 0.5rem 1.2rem rgba(153,153,153,0.3)}

.navbar__list{display:flex}.navbar__list-item{margin-right:2.5rem}.navbar__list-item:last-child{margin-right:0}.navbar__link{color:#999;cursor:pointer;-webkit-text-decoration:underline rgba(0,0,0,0);text-decoration:underline rgba(0,0,0,0);transition:color 200ms ease, -webkit-text-decoration 200ms ease;transition:color 200ms ease, text-decoration 200ms ease;transition:color 200ms ease, text-decoration 200ms ease, -webkit-text-decoration 200ms ease}.navbar__link:hover,.navbar__link--active{color:#fd9d01;-webkit-text-decoration:underline #fd9d01;text-decoration:underline #fd9d01}.navbar__link:focus{outline:0.2rem solid rgba(253,157,1,0.6);outline-offset:.3rem}

.toolbar__wrapper{max-width:140rem;width:100%;margin:0 auto}.toolbar{background-color:rgba(255,255,255,0.9);padding:2.5rem;box-shadow:0 0.8rem 1.5rem rgba(39,39,39,0.2)}.toolbar--sticky{position:-webkit-sticky;position:sticky;top:0;left:0;z-index:2}.toolbar__wrapper{display:flex;justify-content:space-between;align-items:center}.toolbar__title{width:100%;text-align:center;font-size:2.6rem;color:#999}.toolbar__tools{display:flex}.toolbar__input{margin-right:2.5rem}

.loader{font-size:3rem;position:absolute;top:calc(50% - 3rem / 2);left:calc(50% - 3rem / 2);-webkit-animation:spin 1000ms linear infinite;animation:spin 1000ms linear infinite;z-index:4}.loader--orange{color:#fd9d01}.loader--blue{color:#29b6cf}@-webkit-keyframes spin{from{transform:rotate(0)}to{transform:rotate(360deg)}}@keyframes spin{from{transform:rotate(0)}to{transform:rotate(360deg)}}

.backdrop{position:fixed;top:0;left:0;width:100%;height:100%;z-index:3;background-color:rgba(0,0,0,0.9);-webkit-animation:fadeOut 400ms ease forwards;animation:fadeOut 400ms ease forwards}@-webkit-keyframes fadeOut{from{opacity:0}to{opacity:1}}@keyframes fadeOut{from{opacity:0}to{opacity:1}}

.layout{display:flex;flex-direction:column;justify-content:space-between;min-height:100vh}.layout__content{flex-grow:1;background-image:linear-gradient(to right, #4A00E0, #8E2DE2);display:flex}

.home__wrapper{max-width:140rem;width:100%;margin:0 auto}.home__text,.home__movie{background-color:#fff;padding:3rem;border-radius:1.8rem;box-shadow:0 0.5rem 1.2rem rgba(39,39,39,0.2)}.home{padding:2.5rem;display:flex;width:100%}.home__wrapper{max-width:130rem}.home__container{height:100%;display:flex}.home__text{margin:auto}.home__movies{display:grid;grid-template-columns:repeat(3, 1fr);grid-auto-rows:60rem;grid-gap:2.5rem;gap:2.5rem}.home__movie{display:flex;flex-direction:column;justify-content:center;background-size:cover;position:relative}.home__movie::before{content:'';top:0;left:0;width:100%;height:100%;position:absolute;background-color:rgba(0,0,0,0.6);border-radius:1.8rem}.home__movie:hover .home__title{opacity:0}.home__movie:hover .home__link{opacity:1}.home__title{font-size:2.3rem;font-weight:500;color:#fff;text-align:center;margin-bottom:2.5rem;position:relative;z-index:1;transition:opacity 500ms ease}.home__link{margin:0 auto;position:relative;z-index:1;opacity:0;transition:opacity 500ms ease, background-color 200ms ease, color 200ms ease, box-shadow 200ms ease}.home__link:hover{text-decoration:none}

.tooltip.show{font-size:1.6rem;background-color:#fd9d01}.tooltip.show.place-left::after{border-left-color:#fd9d01}.tooltip.show.place-right::after{border-right-color:#fd9d01}.tooltip.show.place-top::after{border-top-color:#fd9d01}.tooltip.show.place-bottom::after{border-bottom-color:#fd9d01}

.similar-movies{position:relative;z-index:1;max-width:15rem;width:100%}.similar-movies__list-item{margin-bottom:2rem}.similar-movies__list-item:last-child{margin-bottom:0}.similar-movies__img{width:100%;display:block}.similar-movies__link:focus{outline-color:#fd9d01}

.full-movie{display:flex;justify-content:center}.full-movie__content{display:flex;background-size:cover;background-repeat:no-repeat;position:relative;padding:3.5rem}.full-movie__content::before{content:'';top:0;left:0;width:100%;height:100%;position:absolute;background-color:rgba(0,0,0,0.7)}.full-movie__body{display:flex;margin-right:3.5rem}.full-movie__img-wrapper,.full-movie__info-wrapper{position:relative;z-index:1}.full-movie__img-wrapper{max-width:50rem;width:100%;flex-shrink:0;margin-right:3.5rem}.full-movie__img{width:100%;display:block}.full-movie__info-wrapper{color:#fff;text-align:center;display:flex;flex-direction:column;justify-content:space-between}.full-movie__title{font-size:2.3rem;margin-bottom:.5rem}.full-movie__date{font-weight:500;display:inline-block;margin-bottom:1.5rem}.full-movie__text{text-align:left;line-height:2.2rem}.full-movie__btn{margin:0 auto}

.profile{width:100%;padding:2.5rem;display:flex}.profile__form{max-width:45rem;width:100%;margin:auto;background-color:#fff;padding:3.5rem;border-radius:2.5rem;box-shadow:0 0.8rem 1.5rem rgba(39,39,39,0.2)}.profile__title{font-size:2.8rem;text-align:center;margin-bottom:2.5rem}.profile__fields{margin-bottom:3.5rem}.profile__input-wrapper{max-width:100%;margin-bottom:3rem}.profile__input-wrapper:last-child{margin-bottom:0}.profile__input{width:100%}.profile__btn{margin:0 auto}

.auth{width:100%;padding:2.5rem;display:flex}.auth__form-wrapper{max-width:45rem;width:100%;margin:auto;background-color:#fff;border-radius:2.5rem;box-shadow:0 0.8rem 1.5rem rgba(39,39,39,0.2)}.auth__tabs{display:flex;border-bottom:0.2rem solid #ebebeb;margin-bottom:3.5rem}.auth__tab{flex-grow:1;border:none;background-color:rgba(0,0,0,0);font-family:"Raleway",sans-serif;font-size:1.8rem;font-weight:700;padding:2rem 3.5rem;outline:none;position:relative;cursor:pointer;color:#999;transition:color 200ms ease}.auth__tab::before{content:'';bottom:-0.2rem;left:0;width:100%;height:.2rem;position:absolute;background-color:rgba(0,0,0,0);transition:background-color 200ms ease}.auth__tab:hover::before{background-color:#a4e2ed}.auth__tab--active{cursor:default;color:#272727}.auth__tab--active:hover::before{background-color:#29b6cf}.auth__tab--active::before{background-color:#29b6cf}.auth__form{padding:0 3.5rem 3.5rem}.auth__field-wrapper{margin-bottom:3.5rem}.auth__input-wrapper{max-width:100%}.auth__input{width:100%}.auth__btn{margin:0 auto}.auth__error{margin-top:.5rem;color:#e71616;font-size:1.4rem}

.page-not-found{width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center;color:#fff}.page-not-found__title{font-size:8rem;text-shadow:0 0.4rem 1rem rgba(39,39,39,0.5);margin-bottom:.5rem}

.pagination{display:flex}.pagination__btn{padding:0;width:4.5rem;height:4.5rem;border-radius:50%;font-size:1.8rem;box-shadow:0 0.5rem 1.2rem rgba(39,39,39,0.4);margin-right:1rem;font-family:"Kanit",sans-serif;color:#272727;transition:none}.pagination__btn:last-child{margin-right:0}.pagination__btn:hover{color:#fd9d01}.pagination__btn--active{background-color:#fd9d01;color:#fff}.pagination__btn--active:hover{color:#fff}

.favorite-movies{padding:2.5rem;width:100%}.favorite-movies__table{border-spacing:0;width:100%;background-color:#ebebeb;border-radius:1rem;box-shadow:0 0.8rem 1.5rem rgba(39,39,39,0.2)}.favorite-movies__tr:last-child .favorite-movies__td{border-bottom:none}.favorite-movies__th,.favorite-movies__td{padding:1.5rem 2.5rem}.favorite-movies__th{background-color:#272727;color:#fff}.favorite-movies__th:first-child{border-radius:1rem 0 0 0}.favorite-movies__th:last-child{border-radius:0 1rem 0 0}.favorite-movies__th--overview{max-width:50rem}.favorite-movies__td{text-align:center;border-bottom:0.1rem dashed #272727}.favorite-movies__td--overview{max-width:50rem;text-align:left}.favorite-movies__link{color:#272727}.favorite-movies__link:hover{text-decoration:none}.favorite-movies__link:hover .favorite-movies__icon,.favorite-movies__link:hover .favorite-movies__text{color:#29b6cf}.favorite-movies__icon{margin-right:1rem;font-size:1.8rem;position:relative;top:.1rem;transition:color 200ms ease}.favorite-movies__text{font-family:"Kanit",sans-serif;transition:color 200ms ease}.favorite-movies__img-wrapper{max-width:15rem;margin:0 auto;box-shadow:0 0.5rem 1.2rem rgba(39,39,39,0.5);border-radius:.8rem}.favorite-movies__img{display:block;width:100%;border-radius:.8rem}.favorite-movies__btn{margin:0 auto}.favorite-movies__pagination{margin-top:3rem}

*{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%;scroll-behavior:smooth}body{font-size:1.6rem;font-family:"Raleway",sans-serif;color:#272727}ul,ol{list-style-type:none}a{color:inherit;text-decoration:none}a:hover{text-decoration:underline}

